/* global chrome */
import React, { useState, useEffect, useRef, useCallback } from "react";
import { motion, AnimatePresence, LayoutGroup } from "framer-motion";
import { useHotkeys } from "react-hotkeys-hook";
import CurrentPageBox from "./CurrentPageBox";
import WorkspacePanel from "./workspacePanel";
import CurrentPageBoxSaveMode from "./CurrentPageBoxSaveMode";
import DashboardButton from "./DashboardButton";
import ToastComp from "./toastComp";
import useStore from "./store";
import ToasterContainer from "./toasterContainer";

const MainContinaer = () => {
  //const newComment = useRef("");
  const setLoadingSel = useStore((state) => state.setLoadingSel);
  const setSelItem = useStore((state) => state.setSelItem);
  const isSaved = useStore((state) => state.isSaved);
  const setIsSaved = useStore((state) => state.setIsSaved);
  const showLayout = useStore((state) => state.showLayout);
  const closePopoverSend = useStore((state) => state.closePopoverSend);
  const darkTheme = useStore((state) => state.darkTheme);
  const setDarkTheme = useStore((state) => state.setDarkTheme);
  const setBlockPopoverClose = useStore((state) => state.setBlockPopoverClose);

  useEffect(() => {
    let OSTheme = window.matchMedia("(prefers-color-scheme: dark)").matches;
    setDarkTheme(OSTheme);
    //console.log("DARK? :", darkTheme, "OSTheme: ", OSTheme);
  }, []);

  const newComment = useRef("");
  const setNewComment = (text) => {
    newComment.current = text;
  };

  let extensionElement = document.getElementById("tabExtend");

  ["keydown", "keyup", "keypress"].forEach((e) =>
    extensionElement.addEventListener(
      e,
      function (e) {
        e.stopPropagation();
      },
      false
    )
  );

  useEffect(() => {
    chrome.runtime.onMessage.addListener(onMessage);
    //let themeState = localStorage.getItem("themeState");
    //console.log("themeState: ", themeState);
  }, []);

  const onMessage = async (req, sender, sendResponse) => {
    if (req.msg === "tabsQueryFromBg") {
      //console.log(req.tab);
      setSelItem(req.tab);
      setLoadingSel(false);
    }
    if (req.msg === "popoverDisabled") {
      setBlockPopoverClose(true);
      //console.log("popoverDisabled -> Setting block to TRUE");
    }
    if (req.msg === "savedStatus") {
      if (req.status === "notSaved") {
        //console.log("Not saved");
        setIsSaved(false);
      } else if (req.status === "isSaved") {
        //console.log("isSaved ", req.data);
        setIsSaved(req.data);
      }
      chrome.runtime.onMessage.removeListener(onMessage);
    }
  };

  const closeCurrentView = () => {
    closePopoverSend();
  };

  useHotkeys(
    "esc",
    () => closeCurrentView(),
    {
      keydown: true,
      keyup: false,
    },
    [showLayout]
  );

  const constraintsRef = useRef(null);

  return (
    <>
      <div
        style={{
          display: "flex",
          position: "fixed",
          top: "0",
          right: "0",
          width: "100%",
          height: "100%",
          alignItems: "center",
          zIndex: "2147483646",
          pointerEvents: "none",
        }}
        onClick={(e) => e.stopPropagation()}
        ref={constraintsRef}
      >
        {showLayout === "popover" && (
          <>
            <CurrentPageBox
              isSaved={isSaved}
              closeCurrentView={closeCurrentView}
              constraintsRef={constraintsRef}
              setNewComment={setNewComment}
              newComment={newComment}
            />
            <DashboardButton />
          </>
        )}
      </div>
      <AnimatePresence>
        {showLayout === "saveView" && (
          <div
            style={{
              position: "fixed",
              display: "flex",
              backgroundColor: "#0c111887",
              zIndex: "2147483645",
              width: "100%",
              height: "100%",
              top: "0",
              left: "0",
              right: "0",
              bottom: "0",
              alignItems: "center",
              backdropFilter: "blur(1px)",
              cursor: "pointer",
            }}
            onClick={() => closePopoverSend()}
          >
            <motion.div
              initial={{ scale: 0.9 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0, x: "50vw", y: "50vh" }}
              style={{
                marginLeft: "auto",
                marginRight: "auto",
                display: "flex",
                flexDirection: "column",
                pointerEvents: "auto",
              }}
            >
              <CurrentPageBoxSaveMode newComment={newComment} setNewComment={setNewComment} />
              <WorkspacePanel newComment={newComment} />
            </motion.div>
          </div>
        )}
      </AnimatePresence>
      <ToasterContainer />
    </>
  );
};

export default MainContinaer;
