/* global chrome */
import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { CloseIcon, DoneIcon, ErrorIcon, Spinner } from "./customIcon";
import useStore from "./store";

const ToastComp = ({ status, text, setLocalShow, closeAfterShown }) => {
  const setShowToast = useStore((state) => state.setShowToast);
  const blockPopoverClose = useStore((state) => state.blockPopoverClose);
  const darkTheme = useStore((state) => state.darkTheme);

  const [hover, setHover] = useState(false);
  const [hidden, hideToasterState] = useState(false);
  const hideToaster = () => {
    hideToasterState(!hidden);
  };

  //let color = status === "loading" ? "#0080FF" : status === "success" ? "#3BDE86" : "#F56565";
  let lightColor = status === "loading" ? "#B2D8FF" : status === "success" ? "#C1F2D7" : "#F9CDCD";
  /* let text =
    status === "loading" ? "Loading..." : status === "success" ? "Site saved" : "Save failed"; */
  const SYMBOL = {
    loading: (
      <Spinner fillColor={"#0080FF"} trackColor={"#B2D8FF"} maxW={"20px"} noMargins={true} />
    ),
    success: <DoneIcon />,
    error: <ErrorIcon />,
  };

  const tempMessTiming = { duration: 2.6, times: [0, 0.04, 0.12, 0.9, 1] };
  let isTempMess = status === "success" || status === "error" ? true : false;

  const closePopoverSendFromToast = () => {
    chrome.runtime.sendMessage({ msg: "closeFromToaster" });
  };

  const toasterEnd = () => {
    if (closeAfterShown) {
      closePopoverSendFromToast();
    }
    if (isTempMess) {
      setShowToast(false);
      setLocalShow(false);
    } else return;
  };

  return (
    <motion.div
      //layoutId="savedBox"
      initial={{ y: 106 }}
      animate={{ y: hidden ? 106 : isTempMess ? [0, -12, 0, 0, 106] : 0 }}
      transition={isTempMess ? tempMessTiming : null}
      style={{
        zIndex: "2147483647",
        width: "240px",
        background: darkTheme ? "#262A2D" : "#FFF",
        //boxShadow: "rgb(78 80 81 / 70%) 0px 12px 24px -2px",
        filter: darkTheme
          ? "drop-shadow(0px 16px 20px #00000070"
          : "drop-shadow(0px 16px 20px #4e5051b3)",
        borderRadius: "10px",
        padding: "12px 0px 24px",
        position: "fixed",
        bottom: "16px",
        right: "16px",
        pointerEvents: "auto",
      }}
      onAnimationComplete={() => toasterEnd()}
    >
      <div
        style={{
          width: "100%",
          display: "flex",
          maxHeight: "12px",
          maxWidth: "16px",
          marginLeft: "auto",
          marginRight: "16px",
          cursor: "pointer",
        }}
        onMouseEnter={() => setHover(true)}
        onMouseLeave={() => setHover(false)}
        onClick={() => hideToaster(true)}
      >
        <CloseIcon hoverClose={hover} />
      </div>
      <div style={{ position: "relative", display: "flex" }}>
        <div
          style={{
            width: "4px",
            height: "32px",
            marginRight: "16px",
            borderRadius: "0px 4px 4px 0px",
            backgroundColor: lightColor,
            opacity: darkTheme ? "0.36" : "1",
          }}
        />
        {SYMBOL[status]}
        <p
          style={{
            margin: "auto 12px",
            fontWeight: "500",
            fontSize: "15px",
            color: darkTheme ? "#FFF" : "#1a202c",
          }}
        >
          {text}
        </p>
      </div>
    </motion.div>
  );
};

export default ToastComp;
