/* global chrome */
import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { CloseIcon, CopySel, DragHandleIcon, Spinner, TrashIcon } from "../customIcon";
import TextAreaComp from "./TextAreaComp";
import TooltipComp from "../TooltipComp";
import MiniToolBar from "./MiniToolBar";
import useStore from "../store";

const StackedItems = ({ items, currentUrl }) => {
  const [showTooltip, setShowTooltip] = useState(false);
  const darkTheme = useStore((state) => state.darkTheme);
  const openUrl = (url) => {
    //console.log("openUrl", url);
    chrome.runtime.sendMessage({ msg: "openUrl", url: url });
  };
  const timingProp = { duration: 0.1 };

  return (
    <div style={{ display: "flex", padding: "2px 20px 16px", flexWrap: "wrap" }}>
      {items
        .filter((site) => site.url !== currentUrl)
        .map((site, index) => (
          <motion.div
            whileHover={{ scale: 1.075 }}
            whileTap={{ scale: 0.9925 }}
            transition={timingProp}
            onClick={() => openUrl(site.url)}
            key={index}
            style={{
              display: "flex",
              alignItems: "center",
              borderRadius: "7px",
              minWidth: "32px",
              minHeight: "32px",
              maxHeight: "32px",
              maxWidth: "32px",
              backgroundColor: darkTheme === true ? "#30363a" : "#FFF",
              boxShadow: darkTheme ? "0px 2px 5px -1px #050505b3" : "0px 2px 5px -1px #4e505166",
              marginRight: "16px",
              marginTop: "10px",
              marginBottom: "4px",
              position: "relative",
            }}
            onMouseEnter={() => setShowTooltip(index)}
            onMouseLeave={() => setShowTooltip(false)}
          >
            {site?.favIcon && site.favIcon !== "localIcon" ? (
              <img
                src={site.favIcon}
                height="24px"
                width="24px"
                style={{
                  fontWeight: "500",
                  margin: "auto",
                  maxWidth: "24px",
                  maxHeight: "24px",
                }}
              />
            ) : (
              <div
                style={{
                  background: darkTheme ? "#373B3D" : "#E6EAEE",
                  fontWeight: "500",
                  width: "24px",
                  height: "24px",
                  maxWidth: "24px",
                  maxHeight: "24px",
                  borderRadius: "6px",
                  margin: "auto",
                }}
              />
            )}
            {showTooltip === index && (
              <TooltipComp top={"-52px"} left={"-34px"}>
                <div style={{ padding: "6px 10px", maxWidth: "160px" }}>
                  <div style={{ display: "flex" }}>
                    <p
                      style={{
                        fontSize: "14px",
                        color: darkTheme ? "#1a202c" : "#FFF",
                        whiteSpace: "nowrap",
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                      }}
                    >
                      {site.title}
                    </p>
                  </div>
                  <p
                    style={{
                      fontSize: "12px",
                      color: darkTheme ? "#1a202c" : "#FFF",
                      marginTop: "2px",
                      opacity: 0.6,
                      whiteSpace: "nowrap",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                    }}
                  >
                    {site.url}
                  </p>
                </div>
              </TooltipComp>
            )}
          </motion.div>
        ))}
    </div>
  );
};
const SavedInBar = ({ setCurrentSelectedColor }) => {
  //global
  const setIsSaved = useStore((state) => state.setIsSaved);
  const modifiedIndexes = useStore((state) => state.modifiedIndexes);
  const isSaved = useStore((state) => state.isSaved);
  const areyousure = useStore((state) => state.areyousure);
  const setAreyousure = useStore((state) => state.setAreyousure);
  const deleteNote = useStore((state) => state.deleteNote);
  const loadingDeleteButton = useStore((state) => state.loadingDeleteButton);
  const setLoadingDeleteButton = useStore((state) => state.setLoadingDeleteButton);
  const focusSavedNote = useStore((state) => state.focusSavedNote);
  const darkTheme = useStore((state) => state.darkTheme);
  //local
  const [hover, setHover] = useState(false);
  const [showTooltip, setShowTooltip] = useState(false);
  const [isBlurred, setFocusBlurred] = useState(false);

  const initialProp = { scale: 0, opacity: 0 };
  const animateProp = { scale: 1, opacity: 1 };
  //const timingProp = { delay: 0.24 /* + index * 0.1 */ };
  const buttonTiming = { duration: 0.1 };

  const deleteNoteMiddleware = (group, index) => {
    setLoadingDeleteButton(true);
    deleteNote(group, index);
  };

  return (
    <motion.div
      layout
      style={{
        padding: "4px 16px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        maxHeight: "62vh",
        overflow: "auto",
      }}
      className={darkTheme ? "groupScrollAreaDarkHover" : "groupScrollAreaHover"}
    >
      {isSaved.map((group, index) => (
        <motion.div
          key={index}
          initial={initialProp}
          animate={animateProp}
          transition={{ delay: 0.24 /* + index * 0.1 */ }}
          onMouseEnter={() => setHover(index)}
          onMouseLeave={() => setHover(false)}
          style={{
            borderRadius: "8px",
            background: darkTheme ? "#33373B" : "#FFF",
            boxShadow:
              focusSavedNote === index && isBlurred === index
                ? "0px 0px 8px 1px #63B3ED"
                : darkTheme
                ? "0px 4px 10px -2px #050505b3"
                : "0 1px 4px 0 rgba(59,60,63,0.60), inset -2px -2px 2px 0 rgba(230,230,230,0.80), inset 0 1px 2px 0 rgba(255,255,255,0.50)",
            marginBottom: "8px",
            marginTop: "8px",
            width: "100%",
            //filter: areyousure === index ? "0.5" : "1",
            //position: "relative",
          }}
        >
          <div
            style={{
              display: "flex",
              margin: "8px 16px 8px 20px",
              minHeight: "32px",
              alignItems: "center",
            }}
          >
            <div
              style={{ display: "flex", position: "relative", alignItems: "center" }}
              onMouseEnter={() => setShowTooltip(index)}
              onMouseLeave={() => setShowTooltip(false)}
            >
              {group.emoji?.length > 10 ? (
                <img
                  src={group.emoji}
                  height="16px"
                  width="16px"
                  style={{
                    zIndex: "1",
                    userSelect: "none",
                    borderRadius: "4px",
                    marginTop: "2px",
                  }}
                />
              ) : (
                <p style={{ fontSize: "16px", marginTop: "2px" }}>{group.emoji}</p>
              )}
              <p
                style={{
                  fontSize: "14px",
                  marginRight: "8px",
                  marginLeft: "12px",
                  marginTop: "1px",
                  fontWeight: "500",
                  color: darkTheme ? "#FFF" : "#1a202c",
                }}
              >
                {group.title}
              </p>
              {modifiedIndexes.includes(index) && (
                <p style={{ fontSize: "24px", color: "#bac5d0", marginTop: "-2px" }}>•</p>
              )}
              {showTooltip === index && (
                <TooltipComp top={"35px"} left={"-24px"}>
                  <div style={{ padding: "6px 10px" }}>
                    <div style={{ display: "flex" }}>
                      <p
                        style={{
                          opacity: "0.75",
                          marginRight: "2px",
                          fontSize: "14px",
                          color: darkTheme ? "#1a202c" : "#FFF",
                        }}
                      >
                        Saved in:
                      </p>
                      <p style={{ fontSize: "14px", color: darkTheme ? "#1a202c" : "#FFF" }}>
                        {group.inCategory}
                      </p>
                    </div>
                    <p
                      style={{
                        fontSize: "12px",
                        color: darkTheme ? "#1a202c" : "#FFF",
                        marginTop: "2px",
                        opacity: 0.6,
                      }}
                    >
                      {group.itemObj.dateCreated.substring(15, 21)},{" "}
                      {group.itemObj.dateCreated.substring(4, 15)}
                    </p>
                    {modifiedIndexes.includes(index) && (
                      <p
                        style={{
                          fontSize: "12px",
                          color: darkTheme ? "#1a202c" : "#FFF",
                          marginTop: "2px",
                        }}
                      >
                        • Changes not saved
                      </p>
                    )}
                  </div>
                </TooltipComp>
              )}
            </div>
            {hover === index && (
              <MiniToolBar
                setAreyousure={setAreyousure}
                areyousure={areyousure}
                index={index}
                note={group.itemObj.comment}
                isStacked={group.isStacked}
              />
            )}
            {areyousure === index && (
              <>
                <div
                  style={{
                    position: "absolute",
                    top: "0",
                    right: "0",
                    height: "100%",
                    width: "100%",
                    background: "#000",
                    opacity: "0.5",
                    zIndex: "10000",
                    borderRadius: "12px",
                  }}
                  onClick={() => setAreyousure(false)}
                />
                <div
                  style={{
                    position: "absolute",
                    right: "calc(50% - 138px)",
                    top: "calc(50% - 67px)",
                    background: darkTheme ? "#464c52" : "#FFF",
                    borderRadius: "10px",
                    zIndex: "999999999",
                    padding: "10px 10px 10px 16px",
                    boxShadow: "0px 6px 20px -4px #262626b3",
                    width: "236px",
                    textAlign: "left",
                  }}
                >
                  <p
                    style={{
                      fontSize: "14px",
                      fontWeight: "500",
                      marginTop: "12px",
                      color: darkTheme ? "#FFF" : "#1a202c",
                    }}
                  >
                    Delete from {group.emoji?.length > 10 ? "◩" : group.emoji} {group.title}
                  </p>
                  <p
                    style={{
                      fontSize: "12px",
                      marginTop: "6px",
                      marginBottom: "16px",
                      lineHeight: "16px",
                      color: darkTheme ? "#FFF" : "#1a202c",
                      opacity: "0.8",
                    }}
                  >
                    Are you sure you want to delete this saved site and note?
                  </p>
                  <div style={{ display: "flex", width: "100%" }}>
                    <motion.button
                      whileHover={{ backgroundColor: darkTheme ? "#292D2F" : "#cad1d8" }}
                      transition={buttonTiming}
                      onClick={() => setAreyousure(false)}
                      style={{
                        marginLeft: "auto",
                        background: darkTheme ? "#373B3D" : "#E6EAEE",
                        color: darkTheme ? "#FFF" : "#1a202c",
                      }}
                    >
                      Cancel
                    </motion.button>
                    <motion.button
                      whileHover={{ backgroundColor: "#E53E3E" }}
                      transition={buttonTiming}
                      style={{
                        marginLeft: "4px",
                        background: "#F56565",
                        opacity: loadingDeleteButton ? "0.7" : "1",
                        minWidth: "72px",
                        minHeight: "32px",
                        maxHeight: "32px",
                      }}
                      onClick={() => deleteNoteMiddleware(group, index)}
                    >
                      {loadingDeleteButton ? (
                        <Spinner fillColor={"#FFF"} trackColor={"#FC8181"} maxW={"16px"} />
                      ) : (
                        "Delete"
                      )}
                    </motion.button>
                  </div>
                </div>
              </>
            )}
          </div>
          {group.isStacked ? (
            <StackedItems items={group.isStacked} currentUrl={group.itemObj.url} />
          ) : (
            <TextAreaComp
              initValue={group.itemObj.comment ? group.itemObj.comment : null}
              commentColor={group.itemObj.commentColor}
              isTodo={group.itemObj.todo ? true : false}
              doneStatus={group.itemObj.doneStatus}
              index={index}
              setCurrentSelectedColor={setCurrentSelectedColor}
              setFocusBlurred={setFocusBlurred}
            />
          )}
        </motion.div>
      ))}
    </motion.div>
  );
};

export default SavedInBar;
