import React, { useState, useEffect } from "react";
import { CheckDone } from "../customIcon";
import { commentBg } from "../globalStyles";
import { motion, AnimatePresence } from "framer-motion";

const TodoCheck = ({ isDone, commentColor, toggleDone }) => {
  const checkTiming = { duration: 0.16 };
  return (
    <motion.button
      onClick={(e) => toggleDone(e)}
      transition={checkTiming}
      animate={{ scale: 0.825 }}
      whileHover={{ scale: 1 }}
      whileTap={{
        scale: 0.925,
        /* boxShadow:
          "0 1px 10px -2px rgba(152,169,185,0.50), inset 0px 0px 0px 2px" +
          (commentColor !== false ? commentBg[commentColor].bg : "#63B3ED"), */
      }}
      tran
      style={{
        willChange: "transform",
        marginTop: "6px",
        background: isDone
          ? commentColor !== false && commentColor !== undefined
            ? commentBg[commentColor].iconLight
            : "#63B3ED"
          : "transparent",
        marginRight: "12px",
        height: "28px",
        minWidth: "28px",
        maxWidth: "28px",
        padding: "6px 4px 4px",
        borderRadius: "6px",
        alignItems: "center",
        display: "flex",
        boxShadow:
          "inset 0px 0px 0px 2px " +
          (isDone
            ? commentColor !== false && commentColor !== undefined
              ? commentBg[commentColor].bg
              : "#90CDF4"
            : "#B0B7C1"),
      }}
    >
      <AnimatePresence>
        {isDone && (
          <motion.div
            initial={{ scale: 2 }}
            animate={{ scale: 1 }}
            whileTap={{
              scale: 0.925,
            }}
          >
            <CheckDone isDone={isDone} />
          </motion.div>
        )}
      </AnimatePresence>
    </motion.button>
  );
};

export default TodoCheck;
