/* global chrome */
import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { ArrowDownIcon, DashboardIcon, GlobeIcon, Spinner } from "./customIcon";
import TooltipComp from "./TooltipComp";
import useStore from "./store";

const WsSkeleton = () => {
  const darkTheme = useStore((state) => state.darkTheme);
  let bgColor = darkTheme ? "#373B3D" : "#e2e5e9";
  return (
    <>
      <motion.div
        style={{
          padding: "4px",
          height: "34px",
          borderRadius: "8px",
          width: "33%",
          background: bgColor,
          marginBottom: "12px",
          marginLeft: "4px",
        }}
        initial={{ opacity: 0 }}
        animate={{ opacity: 0.8 }}
      />
    </>
  );
};

const CategorySkeleton = () => {
  const darkTheme = useStore((state) => state.darkTheme);
  let bgColor = darkTheme ? "#373B3D" : "#e2e5e9";
  let cats = [1, 2, 3, 4];
  return (
    <motion.div
      style={{
        height: "100%",
        borderRadius: "10px",
        width: "calc(100% - 20px)",
        background: bgColor,
        marginTop: "12px",
        display: "flex",
        alignItems: "center",
        marginLeft: "4px",
      }}
      initial={{ opacity: 0 }}
      animate={{ opacity: 0.8 }}
    >
      <Spinner fillColor={"#0080FF"} trackColor={"#0080ff30"} />
    </motion.div>
  );
};

const WSrow = ({ usersWorkspaces, selected, changeWorkspace, fontStyle }) => {
  const darkTheme = useStore((state) => state.darkTheme);
  const [focused, setFocused] = useState(null);
  let hoverTiming = { duration: 0.16 };

  return (
    <>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          padding: "4px",
          width: "fit-content",
          gap: "8px",
          marginBottom: "12px",
        }}
        onMouseLeave={() => setFocused(null)}
      >
        {usersWorkspaces.map((ws, index) => (
          <div
            key={ws.id}
            onClick={() => changeWorkspace(index)}
            onFocus={() => setFocused(index)}
            onMouseEnter={() => setFocused(index)}
            style={{
              display: "flex",
              padding: "4px 10px",
              background: "transparent",
              fontFamily: fontStyle,
              alignItems: "center",
              borderRadius: "7px",
              position: "relative",
              //background: selected === index ? "#DDE3E9" : "transparent",
            }}
          >
            {ws.emoji?.length > 10 ? (
              <img
                src={ws.emoji}
                height="20px"
                width="20px"
                style={{
                  marginRight: "12px",
                  zIndex: "1",
                  userSelect: "none",
                  borderRadius: "4px",
                }}
              />
            ) : (
              <p
                style={{
                  fontSize: "16px",
                  fontFamily: fontStyle,
                  fontWeight: "500",
                  marginRight: "12px",
                  zIndex: "1",
                  userSelect: "none",
                }}
              >
                {ws.emoji}
              </p>
            )}
            <p
              style={{
                color: darkTheme ? "#FFF" : "#1a202c",
                fontSize: "14px",
                fontFamily: fontStyle,
                whiteSpace: "nowrap",
                fontWeight: "500",
                letterSpacing: "0.12px",
                zIndex: "1",
                userSelect: "none",
              }}
            >
              {ws.name}
            </p>
            {selected === index ? (
              <motion.div
                layoutScroll
                layoutId="sel"
                style={{
                  position: "absolute",
                  bottom: "-8px",
                  left: "0",
                  background: "#499EFF",
                  height: "3px",
                  width: "100%",
                  borderTopLeftRadius: "2px",
                  borderTopRightRadius: "2px",
                }}
              />
            ) : null}
            {focused === index ? (
              <motion.div
                layoutScroll
                layoutId="hover"
                transition={hoverTiming}
                style={{
                  position: "absolute",
                  background: darkTheme ? "#494C4F" : "#E8ECF0",
                  top: "0",
                  left: "0",
                  height: "100%",
                  width: "100%",
                  borderRadius: "7px",
                  zIndex: "0",
                }}
              />
            ) : null}
          </div>
        ))}
      </div>
    </>
  );
};

const NewGroup = ({ catId, sharedSlug, newComment, refId }) => {
  const [hover, setHover] = useState(false);
  const createNewGroup = useStore((state) => state.createNewGroup);

  return (
    <motion.div
      style={{
        width: "100%",
        borderRadius: "10px",
        minHeight: "62px",
        boxShadow: "0 0 0 2px #B0B7C1",
        background: "rgba(176, 183, 193, 0.12)",
        display: "flex",
        marginBottom: "8px",
        alignItems: "center",
        paddingTop: "4px",
        paddingBottom: "4px",
        position: "relative",
        opacity: "0.35",
      }}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      onClick={() => createNewGroup(catId, sharedSlug, newComment.current, refId)}
      whileTap={{ scale: 0.925 }}
      initial={{ scale: 0.925 }}
      animate={{ scale: 1 }}
      exit={{ scale: 0.925 }}
      whileHover={{
        boxShadow: "0 0 0 2px #41E18B",
        background: "rgba(65, 225, 139, 0.1)",
        opacity: "1",
      }}
    >
      {hover && (
        <>
          <motion.div
            style={{ margin: "auto" }}
            initial={{ y: -30, scale: 0 }}
            animate={{ y: 4, scale: 1.375 }}
            //transition={{ delay: 0.06 }}
          >
            <ArrowDownIcon />
          </motion.div>
          <TooltipComp label={"Save to new group"} top={"80px"} left={"calc(50% - 62px)"} />
        </>
      )}
    </motion.div>
  );
};

const MiniGroup = ({ title, emoji, id, itemsLength, WSid, sharedSlug, newComment }) => {
  //global
  const saveToGroup = useStore((state) => state.saveToGroup);
  const darkTheme = useStore((state) => state.darkTheme);

  const [hover, setHover] = useState(false);
  const [brokenImg, setBrokenImg] = useState(false);
  let hoverTiming2 = { duration: 0.12 };
  /* const saveMiddleware = () => {
    let index = tabData.findIndex((group) => group.id === id);
    saveToGroup(WSid, index, sharedSlug);
  }; */

  return (
    <motion.div
      style={{
        width: "100%",
        borderRadius: "10px",
        //height: "32px",
        boxShadow: darkTheme
          ? "0 6px 10px -2px rgba(0,0,0,0.28), 0 4px 16px -1px rgba(0,0,0,0.18)"
          : "0 6px 10px -2px rgba(165,182,185,0.24), 0 4px 16px -1px rgba(171,174,181,0.12)",
        background: darkTheme ? "#262A2D" : "#FFF",
        display: "flex",
        marginBottom: "8px",
        alignItems: "center",
        paddingTop: "4px",
        paddingBottom: "4px",
        position: "relative",
        minWidth: "110px",
      }}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      onClick={() => saveToGroup(WSid, id, sharedSlug, newComment.current)}
      whileTap={{ scale: 0.925 }}
      //whileHover={{ boxShadow: "0 0 0 2px #41E18B" }}
    >
      {emoji?.length > 10 ? (
        brokenImg ? (
          <div
            style={{
              background: "transparent",
              marginTop: "2px",
              padding: "8px 16px",
              height: "32px",
              width: "32px",
            }}
          />
        ) : (
          <img
            src={emoji}
            onError={() => setBrokenImg(true)}
            height="32px"
            width="32px"
            style={{
              zIndex: "1",
              userSelect: "none",
              borderRadius: "4px",
              marginTop: "2px",
              padding: "8px 16px",
              opacity: hover ? "0.5" : "1",
            }}
          />
        )
      ) : (
        <p
          style={{
            fontSize: "32px",
            fontWeight: "500",
            padding: "8px 16px",
            marginTop: "2px",
            opacity: hover ? "0.5" : "1",
          }}
        >
          {emoji}
        </p>
      )}
      <div style={{ display: "flex", flexDirection: "column", maxWidth: "40%" }}>
        <p
          style={{
            fontSize: "16px",
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
            fontWeight: "600",
            opacity: hover ? "0.5" : "1",
            color: darkTheme ? "#FFF" : "#1a202c",
          }}
        >
          {title}
        </p>
        <p
          style={{
            fontSize: "12px",
            fontWeight: "400",
            color: "rgb(176, 183, 193)",
            fontWeight: "500",
            fontSize: "12px",
            marginTop: "2px",
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
          }}
        >
          {itemsLength} {itemsLength === 1 ? "item" : "items"}
        </p>
      </div>
      {hover && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={hoverTiming2}
            style={{
              borderRadius: "10px",
              boxShadow: "0 0 0 2px #41E18B",
              height: "100%",
              width: "100%",
              position: "absolute",
              top: "0",
              left: "0",
              background: "rgb(65, 225, 139, 0.1)",
              display: "flex",
              alignItems: "center",
              //backdropFilter: "blur(1px)",
            }}
          >
            <motion.div
              style={{ margin: "auto" }}
              initial={{ y: -30, scale: 0 }}
              animate={{ y: 4, scale: 1.375 }}
            >
              <ArrowDownIcon />
            </motion.div>
          </motion.div>
          <TooltipComp label={"Save to group"} top={"80px"} left={"calc(50% - 49px)"} />
        </>
      )}
    </motion.div>
  );
};

const CategoriesPanel = ({ tabData, newComment, refId }) => {
  const darkTheme = useStore((state) => state.darkTheme);
  let categoriesToShow = tabData.categories;
  let columnWidth = 100 / categoriesToShow.length;
  let columnWidthString = "calc(" + columnWidth + "% - 12px)";
  const [hoverCat, setHoverCat] = useState(null);

  //categories.map -> cats
  //const toShow = tabs.filter(
  //  (space) => space.categoryID === currentCategoryID
  //  );

  return (
    <div style={{ display: "flex", height: "calc(100% - 50px)" }}>
      {categoriesToShow.map((cat, index) => (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            marginTop: "16px",
            width: columnWidthString,
            marginRight: "12px",
          }}
        >
          <div style={{ display: "flex", opacity: darkTheme ? "0.5" : "1" }}>
            {cat.shared && <GlobeIcon />}
            <p
              style={{
                fontSize: "15px",
                color: "#b0b7c1",
                marginBottom: "12px",
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis",
              }}
            >
              {cat.name.length > 0 ? cat.name : "Untitled"}
            </p>
          </div>
          <div
            className={darkTheme ? "groupScrollAreaDarkHover" : "groupScrollAreaHover"}
            style={{
              display: "flex",
              flexDirection: "column",
              background: darkTheme ? "#191B1E" : "#E8ECF0",
              marginTop: "4px",
              borderRadius: "10px",
              padding: "12px 16px",
              overflow: "auto",
              height: "100%",
              position: "relative",
            }}
            onMouseEnter={() => setHoverCat(index)}
            onMouseOver={() => setHoverCat(index)}
            onMouseLeave={() => setHoverCat(null)}
          >
            {tabData.tabData.length > 0 ? (
              tabData.tabData
                .filter((group) => group.categoryID === cat.id)
                .map((item) => (
                  <MiniGroup
                    tabData={tabData.tabData}
                    WSid={tabData.WSid}
                    key={item.id}
                    title={item.title}
                    emoji={item.emoji}
                    itemsLength={item.tabs?.length}
                    id={item.id}
                    sharedSlug={cat.slug ? cat.slug : false}
                    newComment={newComment}
                  />
                ))
            ) : (
              <p
                style={{
                  opacity: darkTheme ? "0.5" : "1",
                  fontSize: "14px",
                  color: "#b0b7c1",
                  fontWeight: "500",
                  letterSpacing: "0.28px",
                  margin: "auto",
                  position: "absolute",
                  top: "46%",
                  left: "calc(50% - 62px)",
                }}
              >
                EMPTY WORKSPACE
              </p>
            )}

            <AnimatePresence>
              {hoverCat === index && (
                <NewGroup
                  catId={cat.id}
                  sharedSlug={cat.slug ? cat.slug : false}
                  newComment={newComment}
                  refId={refId}
                />
              )}
            </AnimatePresence>
          </div>
        </div>
      ))}
    </div>
  );
};

const WorkspacePanel = ({ newComment }) => {
  //global
  const loadingWSdata = useStore((state) => state.loadingWSdata);
  const setloadingWSData = useStore((state) => state.setloadingWSData);
  const darkTheme = useStore((state) => state.darkTheme);

  let fontStyle =
    "-apple-system, system-ui, BlinkMacSystemFont, Segoe UI, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol";
  const inAnimation = {
    scale: 1,
    //y: "-12vh",
    y: 0,
    //transition: { delay: 0.1 },
  };
  const [selected, setSelected] = useState();
  const [loadingTabData, setLoadingTabData] = useState(false);
  const [usersWorkspaces, setUsersWorkspaces] = useState();
  const [tabData, setTabData] = useState();
  const [refId, setRefId] = useState();

  const fetchUserAndWsData = () => {
    chrome.runtime.sendMessage({ msg: "fetchUserAndWsData_pop" }, (resp) => {
      //console.log(
      //   "userData: ",
      //   resp.firstCargo,
      //   "workspace: ",
      //   resp.secondCargo,
      //   "refid: ",
      //   resp.thirdCargo
      // );
      setUsersWorkspaces(resp.firstCargo);
      setTabData(resp.secondCargo);
      setRefId(resp.thirdCargo);
      let WSid = resp.secondCargo.WSid;
      let index = resp.firstCargo.findIndex((ws) => ws.id === WSid);
      setSelected(index);
      setloadingWSData(false);
    });
  };

  const getWorkspaceData = (WSid) => {
    chrome.runtime.sendMessage({ msg: "getWSdataTo_pop", WSid: WSid }, (resp) => {
      //console.log("workspaceData: ", resp.secondCargo, "refid: ", resp.thirdCargo);
      setTabData(resp.secondCargo);
      setRefId(resp.thirdCargo);
      setLoadingTabData(false);
    });
  };
  const changeWorkspace = (index) => {
    setLoadingTabData(true);
    setSelected(index);
    getWorkspaceData(usersWorkspaces[index].id);
  };

  useEffect(() => {
    if (!usersWorkspaces) {
      fetchUserAndWsData();
    }
  }, []);

  /* useEffect(() => {
    if (!selected && usersWorkspaces && tabData) {
      let WSid = tabData.WSid;
      let index = usersWorkspaces.findIndex((ws) => ws.id === WSid);
      setSelected(index);
    } else return;
  }, [tabData]); */
  const clickOnPopover = (e) => {
    e.stopPropagation();
  };

  return (
    <motion.div
      onClick={(e) => clickOnPopover(e)}
      initial={{ scale: 0.8, y: 72 }}
      animate={inAnimation}
      className={darkTheme ? "groupScrollAreaDarkHover" : "groupScrollAreaHover"}
      style={{
        display: "flex",
        flexDirection: "column",
        height: "60vh",
        width: "80vw",
        background: darkTheme ? "#33373B" : "#F4F5F7",
        padding: "20px",
        paddingRight: "8px",
        borderRadius: "22px",
        marginTop: "24px",
        //boxShadow: "inset 2px 2px 2px 0 #FFFFFF, inset -2px -2px 2px 0 rgba(182,184,186,0.70), rgb(78 80 81 / 60%) 0px 6px 20px -2px",
        filter: darkTheme
          ? "drop-shadow(0px 12px 16px #00000070)"
          : "drop-shadow(0px 12px 16px #4e5051b3)",
        cursor: "pointer",
        overflow: "auto",
        pointerEvents: "auto",
      }}
    >
      {loadingWSdata ? (
        <WsSkeleton />
      ) : (
        <WSrow
          usersWorkspaces={usersWorkspaces}
          selected={selected}
          changeWorkspace={changeWorkspace}
          fontStyle={fontStyle}
        />
      )}
      {loadingTabData || loadingWSdata ? (
        <CategorySkeleton />
      ) : (
        <CategoriesPanel tabData={tabData} newComment={newComment} refId={refId} />
      )}
    </motion.div>
  );
};

export default WorkspacePanel;
