import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { CloseIcon, DoneIcon, ErrorIcon, Spinner } from "./customIcon";
import ToastComp from "./toastComp";
import useStore from "./store";

const ToasterContainer = () => {
  const showToast = useStore((state) => state.showToast);
  const setShowToast = useStore((state) => state.setShowToast);
  const setBlockPopoverClose = useStore((state) => state.setBlockPopoverClose);
  const [localShow, setLocalShow] = useState(false);

  useEffect(() => {
    if (showToast.status) {
      initToaster(showToast);
    }
  }, [showToast]);

  const initToaster = (showToast) => {
    //console.log("show: ", showToast);
    setLocalShow(true);
    setBlockPopoverClose(false);
  };

  if (!localShow || showToast.status === false) return null;

  return (
    <ToastComp
      status={showToast.status}
      setLocalShow={setLocalShow}
      text={showToast.text}
      closeAfterShown={showToast.closeAfterShown}
    />
  );
};

export default ToasterContainer;
