import React, { useState, useEffect, useRef, useLayoutEffect } from "react";
import { ExpandArrow } from "../customIcon";
import { commentBg, fontStyle } from "../globalStyles";
import TodoCheck from "./TodoCheck";
import { motion, AnimatePresence } from "framer-motion";
import useStore from "../store";

function useRunAfterUpdate() {
  const afterPaintRef = React.useRef(null);
  useLayoutEffect(() => {
    if (afterPaintRef.current) {
      afterPaintRef.current();
      afterPaintRef.current = null;
    }
  });
  const runAfterUpdate = (fn) => (afterPaintRef.current = fn);
  return runAfterUpdate;
}

const TextAreaComp = ({
  initValue,
  newNote,
  commentColor,
  isLoading,
  doneStatus,
  isTodo,
  index,
  setCurrentSelectedColor,
  setFocusBlurred,
  maxRowValue = 16,
  setNewComment,
}) => {
  //globalState
  const modifySelectedNote = useStore((state) => state.modifySelectedNote);
  //const setNewComment = useStore((state) => state.setNewComment);
  const bringBackFocus = useStore((state) => state.bringBackFocus);
  const reFocus = useStore((state) => state.reFocus);
  const setFocusSavedNote = useStore((state) => state.setFocusSavedNote);
  const focusSavedNote = useStore((state) => state.focusSavedNote);
  const closePopoverSend = useStore((state) => state.closePopoverSend);
  const darkTheme = useStore((state) => state.darkTheme);
  //localState
  const [textAreaRows, setTextAreaRows] = useState(2);
  const runAfterUpdate = useRunAfterUpdate();
  const [value, setValue] = useState(initValue);
  const [innerTodo, setInnerTodo] = useState(isTodo);
  const [isDone, setIsDone] = useState(doneStatus);
  const [fiveKhit, setfiveKhit] = useState(false);

  useEffect(() => {
    if (isTodo !== innerTodo) {
      setInnerTodo(isTodo);
    }
  }, [isTodo]);

  useEffect(() => {
    if (!isLoading && inputRef !== null && newNote) {
      inputRef.current.focus();
    }
  }, [isLoading]);

  const inputRef = useRef(null);

  const focusBlurred = () => {
    index !== -1 && setFocusBlurred(index);
  };

  const setFocusNote = () => {
    commentColor === undefined
      ? setCurrentSelectedColor(false)
      : setCurrentSelectedColor(commentColor);
    if (index !== -1) {
      setFocusBlurred(false);
      setFocusSavedNote(index);
    } else {
      setFocusSavedNote(index);
    }
  };

  useEffect(() => {
    if (reFocus === true && focusSavedNote === index) {
      inputRef.current.focus();
      bringBackFocus(false);
    } else if (reFocus === true && focusSavedNote === undefined && index === -1) {
      bringBackFocus(false);
    }
  }, [reFocus]);

  const toggleDone = (e) => {
    let newStatus = !isDone;
    setIsDone(newStatus);
    modifySelectedNote(newStatus, "doneStatus", index);
    e.stopPropagation();
  };

  useEffect(() => {
    if (initValue && inputRef) {
      const textareaLineHeight = 21;
      const maxRows = maxRowValue;
      //const lines = (initValue.match(/\n/g) || "").length + 1;
      const currentRows = ~~(inputRef.current.scrollHeight / textareaLineHeight);
      let rows = currentRows < maxRows ? currentRows : maxRows;
      setTextAreaRows(rows);
    }
  }, [inputRef]);

  const valueInput = (e) => {
    const textareaLineHeight = 21;
    const minRows = 1;
    const maxRows = maxRowValue;
    let inputLength = e.target.value.length;
    //console.log(e.target.value.length);
    if (fiveKhit && inputLength < 5000) {
      setfiveKhit(false);
    }
    if (inputLength === 5000) {
      setfiveKhit(true);
    }
    if (inputLength > 5000) {
      return;
    }

    const previousRows = e.target.rows;
    e.target.rows = minRows; // reset number of rows in textarea

    const currentRows = ~~(e.target.scrollHeight / textareaLineHeight);

    if (currentRows === previousRows) {
      e.target.rows = currentRows;
    }

    if (currentRows >= maxRows) {
      e.target.rows = maxRows;
      e.target.scrollTop = e.target.scrollHeight;
    }

    let newRows = currentRows < maxRows ? currentRows : maxRows;
    setTextAreaRows(newRows);

    let newVal1 = e.target.value.slice(0, 5000);
    //check for arrows and deduct if so is the case, otherwise just selectionEnd
    let newPos = newVal1.substring(0, newVal1.length - 2).includes("->")
      ? inputRef.current.selectionEnd - 1
      : newVal1.substring(0, newVal1.length - 2).includes("<-")
      ? inputRef.current.selectionEnd - 1
      : inputRef.current.selectionEnd;
    if (newVal1.includes("/done")) {
      setIsDone(true);
      modifySelectedNote(true, "doneStatus", index);
    }
    if (newVal1.includes("/todo")) {
      setInnerTodo(!innerTodo);
      modifySelectedNote(!innerTodo, "todo", index);
    }
    /* if (newVal1.includes("/red")) {
      //setCommentColor(0);
    }
    if (newVal1.includes("/orange")) {
      //setCommentColor(1);
    }
    if (newVal1.includes("/green")) {
      //setCommentColor(2);
    } */
    let newVal2 = newVal1.replace("/done", "");
    let newVal3 = newVal2.replace("/todo", "");
    let newVal4 = newVal3.replace("->", "→");
    let newVal5 = newVal4.replace("<-", "←");
    //let newVal6 = newVal5.replace("- ", " • ");
    let newVal6 = newVal5.replace("/red", "");
    let newVal7 = newVal6.replace("/orange", "");
    let newVal8 = newVal7.replace("/green", "");
    let newVal9 = newVal8.slice(0, 2) === "- " ? newVal8.replace("- ", "• ") : newVal8;
    let newVal = newVal9.replace("\n- ", "\n• ");
    setValue(newVal);
    if (index === -1) {
      //modifySelectedNote(newVal, "comment", -1);
      setNewComment(newVal);
    } else {
      modifySelectedNote(newVal, "comment", index);
    }
    runAfterUpdate(() => {
      inputRef.current.selectionStart = newPos;
      inputRef.current.selectionEnd = newPos;
    });
    return;
  };

  const handleKeyPress = (e) => {
    if (e.keyCode === 27) {
      closePopoverSend();
    }
  };

  return (
    <>
      <motion.div style={{ display: "flex", margin: "8px 16px 12px", minHeight: "62px" }}>
        {innerTodo && (
          <TodoCheck commentColor={commentColor} isDone={isDone} toggleDone={toggleDone} />
        )}
        <textarea
          value={value}
          ref={inputRef}
          onFocus={() => setFocusNote()}
          onBlur={() => focusBlurred()}
          onInput={(e) => valueInput(e)}
          onKeyDown={(e) => handleKeyPress(e)}
          className={
            commentColor !== false
              ? "input" + commentColor + " groupScrollAreaDarkHover"
              : darkTheme
              ? "textareaDark groupScrollAreaDarkHover"
              : "groupScrollAreaHover"
          }
          placeholder={newNote ? (innerTodo ? (isDone ? "" : "New to-do") : "New note") : ""}
          autofocus={false}
          maxlength="5000"
          rows={textAreaRows}
          spellCheck="false"
          style={{
            resize: "none",
            width: "calc(100% - 32px)",
            borderRadius: "8px",
            border: "none",
            padding: "16px",
            paddingTop: "12px",
            textDecoration: isDone && innerTodo ? "line-through" : "initial",
            opacity: isDone && innerTodo ? "0.65" : "1",
            fontFamily: fontStyle,
            width: "100%",
            minHeight: "12px",
            transition: "0.3s ease",
            filter: darkTheme ? "brightness(0.8) contrast(1.2)" : "",
          }}
        ></textarea>
      </motion.div>
      {fiveKhit && (
        <p style={{ fontSize: "12px", color: "#E53E3E", margin: "0px 32px 12px" }}>
          Maximum length exceeded, Note truncated to 5000 characters
        </p>
      )}
    </>
  );
};

export default TextAreaComp;
