import React, { useState } from "react";
import { motion } from "framer-motion";
import { CloseIcon } from "./customIcon";
import { TitleSkeleton } from "./Popoverbox/skeletonLoaders";
import { fontStyle } from "./globalStyles";
import TextAreaComp from "./Popoverbox/TextAreaComp";
import useStore from "./store";

const CurrentPageBoxSaveMode = ({ newComment, setNewComment }) => {
  const darkTheme = useStore((state) => state.darkTheme);

  const closePopoverSend = useStore((state) => state.closePopoverSend);
  const selectedItem = useStore((state) => state.selectedItem);
  const isLoadingSel = useStore((state) => state.isLoadingSel);
  const isTodo = useStore((state) => state.isNewNoteTodo);
  //const comment = useStore((state) => state.newComment);
  const doneStatus = useStore((state) => state.currentTodoStatus);
  const commentColor = useStore((state) => state.commentColor);

  const [hoverClose, setHoverClose] = useState(false);
  const clickOnPopover = (e) => {
    e.stopPropagation();
  };
  const clickOnClose = () => {
    closePopoverSend();
    //setShowLayout(false);
  };

  let timingProp = { delay: 0.28 };
  let timingProp2 = { delay: 0.36 };

  return (
    <motion.div
      initial={{
        scale: 1,
      }}
      animate={{
        scale: 0.9425,
      }}
      style={{
        opacity: "1",
        pointerEvents: "auto",
        marginLeft: "auto",
        marginRight: "auto",
        marginBottom: "auto",
        zIndex: "300",
        height: "auto",
        width: "100%",
        maxWidth: "420px",
        minWidth: "284px",
        background: darkTheme ? "#262A2D" : "#FFF",
        borderRadius: "12px",
        padding: "10px",
        //overflow: "hidden",
        padding: "10px 4px 4px",
        cursor: "pointer",
        border: "4px solid #44E18C",
        //boxShadow: "0 0 0 4px #44E18C, 0px 12px 24px -2px #4e5051b3",
        filter:
          darkTheme === true
            ? "drop-shadow(0px 8px 16px #050505b3)"
            : "drop-shadow(0px 8px 16px #4e5051b3)",
      }}
      onClick={(e) => clickOnPopover(e)}
    >
      <div
        style={{
          display: "flex",
          width: "100%",
          marginRight: "16px",
          opacity: darkTheme === true ? "0.55" : "1",
        }}
      >
        <div
          style={{ marginLeft: "auto", marginRight: "6px" }}
          onClick={() => clickOnClose()}
          onMouseEnter={() => setHoverClose(true)}
          onMouseLeave={() => setHoverClose(false)}
        >
          <CloseIcon hoverClose={hoverClose} />
        </div>
      </div>
      <div
        style={{
          display: "flex",
          margin: "0px 18px 12px",
          maxWidth: "100%",
        }}
      >
        <motion.div
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          transition={timingProp}
          style={{
            display: "flex",
            alignItems: "center",
            borderRadius: "8px",
            minWidth: "34px",
            minHeight: "34px",
            maxHeight: "34px",
            maxWidth: "34px",
            backgroundColor: darkTheme ? "#30363a" : "#FFF",
            boxShadow: darkTheme ? "0px 2px 8px -1px #050505b3" : "0px 2px 6px -1px #4e505166",
            marginRight: "16px",
          }}
        >
          {selectedItem?.favIconUrl ? (
            <img
              src={selectedItem.favIconUrl}
              height="24px"
              width="24px"
              style={{ fontWeight: "500", margin: "auto", maxWidth: "24px", maxHeight: "24px" }}
            />
          ) : (
            <div
              style={{
                background: darkTheme ? "#373B3D" : "#E6EAEE",
                fontWeight: "500",
                width: "24px",
                height: "24px",
                maxWidth: "24px",
                maxHeight: "24px",
                borderRadius: "6px",
                margin: "auto",
              }}
            />
          )}
        </motion.div>
        <motion.div
          initial={{ opacity: 0, x: -12 }}
          animate={{ opacity: 1, x: 0 }}
          transition={timingProp2}
          style={{ maxWidth: "84%" }}
          /* onAnimationComplete={() => {
            setIsLoading(false);
          }} */
        >
          {isLoadingSel ? (
            <TitleSkeleton />
          ) : (
            <div style={{ maxWidth: "100%" }}>
              <p
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  marginTop: "auto",
                  marginBottom: "auto",
                  letterSpacing: "0.10px",
                  color: darkTheme ? "#FFF" : "#1a202c",
                  fontWeight: "500",
                  fontSize: "15px",
                  //fontFamily: fontStyle,
                }}
              >
                {selectedItem && selectedItem.title}
              </p>
              <p
                style={{
                  color: "rgb(176, 183, 193)",
                  fontWeight: "500",
                  fontSize: "11px",
                  marginTop: "2px",
                  marginBottom: "0px",
                  //fontFamily: fontStyle,
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                }}
              >
                {selectedItem && selectedItem.url}
              </p>
            </div>
          )}
        </motion.div>
      </div>
      <TextAreaComp
        maxRowValue={4}
        initValue={newComment ? newComment.current : ""}
        isTodo={isTodo}
        doneStatus={doneStatus}
        commentColor={commentColor}
        setNewComment={setNewComment}
        index={-1}
      />
    </motion.div>
  );
};

export default CurrentPageBoxSaveMode;
