/* global chrome */
import * as React from "react";
import ReactDOM from "react-dom";
import ReactShadowRoot from "react-shadow-root";
import MainContinaer from "./components/mainContainer";
import theme from "./components/styles";

const injectWrapper = document.body;
const app = document.createElement("div");
app.id = "tabExtend";
if (injectWrapper) injectWrapper.appendChild(app);

export class Extension extends React.Component {
  render() {
    return (
      <>
        <style>{theme}</style>
        <MainContinaer />
      </>
    );
  }
}

ReactDOM.render(
  <tabExtend-inner style={{ all: "initial" }}>
    <ReactShadowRoot>
      <Extension />
    </ReactShadowRoot>
  </tabExtend-inner>,
  document.getElementById("tabExtend")
);
