/* global chrome */
import create from "zustand";

const closePopoverSendInner = () => {
  //console.log("INNER CLOSE KÖRS");
  chrome.runtime.sendMessage({ msg: "closePopover" });
};

const modifySelectedNoteInner = (set, get, data, key, index) => {
  //console.log("MOD: ", data, key, index);
  if (index === -1) {
    //Modifing new note
    /* if (key === "comment") {
      //setNewComment(data);
      set({ newComment: data });
      //newComment.current = data;
      return;
    } */
    if (key === "comment") {
      //set({ newComment: data });
      //newComment.current = data;
    }
    if (key === "todo") {
      get().setNewNoteTodo(data);
    }
    if (key === "doneStatus") {
      get().setCurrentTodoStatus(data);
    }
  } else {
    if (get().originalArr === false) {
      let savedMapped = get().isSaved.map((item) => item.itemObj.comment);
      set({ originalArr: savedMapped });
    }
    let newData = get().isSaved;
    newData[index].itemObj[key] = data;
    newData[index].itemObj["note"] = true;
    if (key === "todo") {
      if (!data) {
        newData[index].itemObj.doneStatus = data;
      }
    }
    if (key === "doneStatus") {
      get().setIsLoadingUpdate(true);
      // const itemToUpdate = isSaved[index];
      newData[index].itemObj.doneStatus = data;
      let msg = "updateItemInWS";
      if (newData[index].sharedRefId) {
        msg = "updateItemInWSShared";
      }
      chrome.runtime.sendMessage(
        {
          msg: msg,
          groupIndex: newData[index].groupIndex,
          itemIndex: newData[index].itemIndex,
          refId: newData[index].refId,
          itemObj: newData[index].itemObj,
          isStacked: newData[index].isStacked,
          stackIndex: newData[index].stackIndex,
          type: "tab",
          ...(newData[index].sharedRefId && { sharedRefId: newData[index].sharedRefId }),
        },
        (res) => {
          if (res.msg === "error") {
            get().setIsLoadingUpdate(false);
          } else {
            let newArr = get().modifiedIndexes.filter((i) => i !== index);
            get().setModifiedIndex(newArr);
            get().setIsLoadingUpdate(false);
            get().bringBackFocus(true);
          }
        }
      );
    }
    if (get().modifiedIndexes.indexOf(index) === -1) {
      let currentModIndex = get().modifiedIndexes;
      let newModArr = [...currentModIndex, index];
      set({ modifiedIndexes: newModArr });
      //get().setModifiedIndex((state) => [...state, index]);
    } else if (key === "comment" && data === get().originalArr[index]) {
      let newArr = get().modifiedIndexes.filter((i) => i !== index);
      get().setModifiedIndex(newArr);
    }
  }
};

const setShowLayoutInner = (get, value) => {
  if (value === "saveView") {
    get().setloadingWSData(true);
  }
  get().setShowLayoutState(value);
};

const saveToGroupInner = (get, set, WSid, groupId, sharedSlug, comment) => {
  get().setShowToast("loading", "Loading...", false);
  set({ showLayout: false });
  let msg = sharedSlug ? "saveToSharedGroup" : "saveToGroup";
  let WSidOrSlug = sharedSlug ? sharedSlug : WSid;
  chrome.runtime.sendMessage(
    {
      msg: msg,
      WSidOrSlug: WSidOrSlug,
      groupId: groupId,
      tab: get().selectedItem,
      comment: comment,
      todo: get().isNewNoteTodo,
      doneStatus: get().currentTodoStatus,
      commentColor: get().commentColor,
    },
    (res) => {
      if (res.msg === "error") {
        get().setShowToast("error", "Error saving site", true);
      } else if (res.msg === "trailLimitReached") {
        get().setShowToast("error", "Trial limit reached", true);
      } else {
        let numberOfSaves = get().isSaved ? (get().isSaved.length + 1).toString() : "1";
        chrome.runtime.sendMessage({
          msg: "setBadge",
          tabId: get().selectedItem.id,
          text: numberOfSaves,
        });
        get().setShowToast("success", "Save complete", true);
      }
    }
  );
};

const createNewGroupInner = (get, set, catId, sharedSlug, comment, refId) => {
  get().setShowToast("loading", "Loading...", false);
  set({ showLayout: false });
  chrome.runtime.sendMessage(
    {
      msg: "saveToNewGroup",
      categoryId: catId,
      tab: get().selectedItem,
      comment: comment,
      todo: get().isNewNoteTodo,
      doneStatus: get().currentTodoStatus,
      commentColor: get().commentColor,
      sharedSlug: sharedSlug,
      refId: refId,
    },
    (res) => {
      if (res.msg === "error") {
        get().setShowToast("error", "Error saving site", true);
      } else if (res.msg === "trailLimitReached") {
        get().setShowToast("error", "Trial limit reached", true);
      } else {
        let numberOfSaves = get().isSaved ? (get().isSaved.length + 1).toString() : "1";
        chrome.runtime.sendMessage({
          msg: "setBadge",
          tabId: get().selectedItem.id,
          text: numberOfSaves,
        });
        get().setShowToast("success", "Group created with item", true);
      }
    }
  );
};

const deleteNoteInner = (get, set, groupData, index) => {
  set({ loadingDeleteButton: true });
  const dateDeleted = new Date().toString();

  let deletedData = [
    {
      deletedTimestamp: dateDeleted,
      deletedType: "item",
      deletedData: groupData.itemObj,
    },
  ];

  let msg = "updateItemInWS";
  if (groupData.sharedRefId) {
    msg = "updateItemInWSShared";
  }
  chrome.runtime.sendMessage(
    {
      msg: msg,
      groupIndex: groupData.groupIndex,
      itemIndex: groupData.itemIndex,
      refId: groupData.refId,
      itemObj: groupData.itemObj,
      isStacked: groupData.isStacked,
      stackIndex: groupData.stackIndex,
      type: "delete",
      ...(groupData.sharedRefId && { sharedRefId: groupData.sharedRefId }),
    },
    (res) => {
      if (res.msg === "error") {
        get().setShowToast("error", "Error removing site", false);
        set({ loadingDeleteButton: false });
      } else {
        chrome.runtime.sendMessage(
          {
            msg: "updateDeletedItems",
            newData: deletedData,
          },
          (res) => {
            if (res.msg === "error") {
              get().setShowToast("error", "Error removing item", false);
              set({ loadingDeleteButton: false });
            } else {
              //let newGroupData = get().isSaved;
              //newGroupData.splice(index, 1);
              //get().setIsSaved(newGroupData);
              //setAreyousure(false);
              let numberOfSaves = get().isSaved ? (get().isSaved.length + -1).toString() : "";
              chrome.runtime.sendMessage({
                msg: "setBadge",
                tabId: get().selectedItem.id,
                text: numberOfSaves,
              });
              get().setShowToast("success", "Item removed", true);
              set({ loadingDeleteButton: false });
              set({ areyousure: false });
              set({ showLayout: false });
            }
          }
        );
        // newGroupData[index].tabData = newData;
      }
    }
  );
};

const useStore = create((set, get) => ({
  darkTheme: null,
  setDarkTheme: (value) => set(() => ({ darkTheme: value })),
  blockPopoverClose: true,
  setBlockPopoverClose: (value) => set(() => ({ blockPopoverClose: value })),
  selectedItem: {},
  setSelItem: (value) => set(() => ({ selectedItem: value })),
  isLoadingSel: false,
  setLoadingSel: (value) => set(() => ({ isLoadingSel: value })),
  isLoadingUpdate: false,
  setIsLoadingUpdate: (value) => set(() => ({ isLoadingUpdate: value })),
  commentColor: false,
  setCommentColor: (value) => set(() => ({ commentColor: value })),
  focusSavedNote: -1,
  setFocusSavedNote: (value) => set(() => ({ focusSavedNote: value })),
  reFocus: false,
  bringBackFocus: (value) => set(() => ({ reFocus: value })),
  isNewNoteTodo: false,
  setNewNoteTodo: (value) => set(() => ({ isNewNoteTodo: value })),
  currentTodoStatus: false,
  setCurrentTodoStatus: (value) => set(() => ({ currentTodoStatus: value })),
  isSaved: "loading",
  setIsSaved: (value) => set(() => ({ isSaved: value })),
  originalArr: false,
  modifiedIndexes: [],
  setModifiedIndex: (value) => set(() => ({ modifiedIndexes: value })),
  showLayout: "popover",
  setShowLayoutState: (value) => set(() => ({ showLayout: value })),
  setShowLayout: (value) => {
    setShowLayoutInner(get, value);
  },
  loadingWSdata: false,
  setloadingWSData: (value) => set(() => ({ loadingWSdata: value })),
  modifySelectedNote: (data, key, index) => {
    modifySelectedNoteInner(set, get, data, key, index);
  },
  //newComment: "",
  //setNewComment: (value) => set(() => ({ newComment: value })),
  saveToGroup: (WSid, groupId, sharedSlug, comment) => {
    saveToGroupInner(get, set, WSid, groupId, sharedSlug, comment);
  },
  toasterStatus: "loading",
  setTosterStatus: (value) => set(() => ({ toasterStatus: value })),
  closePopoverSend: () => {
    closePopoverSendInner();
  },
  createNewGroup: (catId, sharedSlug, comment, refId) => {
    createNewGroupInner(get, set, catId, sharedSlug, comment, refId);
  },
  showToast: false,
  setShowToast: (status, text, closeAfterShown) =>
    set(() => ({ showToast: { status: status, text: text, closeAfterShown: closeAfterShown } })),
  deleteNote: (groupData, index) => {
    deleteNoteInner(get, set, groupData, index);
  },
  areyousure: false,
  setAreyousure: (value) => set(() => ({ areyousure: value })),
  loadingDeleteButton: false,
  setLoadingDeleteButton: (value) => set(() => ({ loadingDeleteButton: value })),
}));

export default useStore;
