import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence, useDragControls } from "framer-motion";
import { useHotkeys } from "react-hotkeys-hook";
import { CloseIcon, DragHandleIcon, Spinner } from "./customIcon";
import SavedInBar from "./Popoverbox/saveInBar";
import { TitleSkeleton } from "./Popoverbox/skeletonLoaders";
import ButtonsRow from "./Popoverbox/ButtonsRow";
import TextInput from "./Popoverbox/TextInput";
import { fontStyle } from "./globalStyles";
import TextAreaComp from "./Popoverbox/TextAreaComp";
import useStore from "./store";

const CurrentPageBox = ({ closeCurrentView, constraintsRef, setNewComment, newComment }) => {
  //globalState
  const darkTheme = useStore((state) => state.darkTheme);
  const selectedItem = useStore((state) => state.selectedItem);
  const isLoadingSel = useStore((state) => state.isLoadingSel);
  const isSaved = useStore((state) => state.isSaved);
  const commentColor = useStore((state) => state.commentColor);
  const isNewNoteTodo = useStore((state) => state.isNewNoteTodo);
  //const comment = useStore((state) => state.newComment);
  //LocalState
  const [isLoading, setIsLoading] = useState(true);
  const [hoverClose, setHoverClose] = useState(false);
  //const [focusSavedNote, setFocusSavedNote] = useState(-1);
  const [currentSelectedColor, setCurrentSelectedColor] = useState(false);

  const dragControls = useDragControls();
  const clickOnPopover = (e) => {
    e.stopPropagation();
  };
  const clickOnClose = () => {
    closeCurrentView();
  };

  return (
    <motion.div
      layout //="position"
      //layoutScroll
      //layoutId="popover"
      drag
      dragMomentum={false}
      dragControls={dragControls}
      dragListener={false}
      dragConstraints={constraintsRef}
      initial={{
        scale: 0,
        y: -324,
        x: -20,
        borderRadius: "12px",
        //boxShadow: "rgb(78 80 81 / 90%) 0px 24px 524px 0px",
        transformOrigin: "50% 0%",
      }}
      animate={{
        scale: 1,
        y: 20,
        x: -20,
        borderRadius: "12px",
        //boxShadow: "rgb(78 80 81 / 70%) 0px 12px 24px -2px",
        //transformOrigin: "50% 50%",
        //opacity: isLoadingSel ? 0 : 1,
      }}
      //transition={{ duration: 0.14 }}
      /* transition={{
        y: { duration: 0.24 },
        default: { duration: 0.2 },
        scale: { delay: 0.1, duration: 0.2 },
        boxShadow: { delay: 0.12, duration: 0.1 },
      }} */
      whileDrag={{ scale: 1.025 }}
      style={{
        pointerEvents: "auto",
        marginLeft: "auto",
        marginBottom: "auto",
        zIndex: "90",
        height: "auto",
        width: "100%",
        maxWidth: "420px",
        minWidth: "284px",
        background: darkTheme ? "#262A2D" : "#FFF",
        padding: "10px",
        padding: "10px 4px 4px",
        cursor: "pointer",
        //boxShadow: "0px 16px 32px -6px #4e5051b3",
        filter:
          darkTheme === true
            ? "drop-shadow(0px 8px 16px #050505b3)"
            : "drop-shadow(0px 8px 16px #4e5051b3)",
      }}
      onClick={(e) => clickOnPopover(e)}
    >
      <motion.div
        layout
        style={{
          display: "flex",
          width: "100%",
          marginRight: "16px",
          opacity: darkTheme === true ? "0.55" : "1",
        }}
      >
        <motion.div
          layout
          style={{
            marginBottom: "8px",
            height: "16px",
            width: "16px",
            marginLeft: "auto",
            marginRight: "auto",
            width: "100%",
            cursor: "grab",
          }}
          whileHover={{ filter: "brightness(0.725)" }}
          whileTap={{ scale: 0.925, cursor: "grabbing" }}
          onPointerDown={(e) => {
            e.stopPropagation();
            e.preventDefault();
            dragControls.start(e);
          }}
        >
          <DragHandleIcon />
        </motion.div>
        <motion.div
          layout
          style={{
            marginLeft: "auto",
            marginRight: "6px",
            willChange: "transform",
          }}
          onClick={() => clickOnClose()}
          onMouseEnter={() => setHoverClose(true)}
          onMouseLeave={() => setHoverClose(false)}
        >
          <CloseIcon hoverClose={hoverClose} />
        </motion.div>
      </motion.div>
      <motion.div
        layout
        style={{
          display: "flex",
          margin: "8px 18px 12px",
          maxWidth: "100%",
        }}
      >
        <motion.div
          layout
          /* initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          transition={{ delay: 0.3 }} */
          style={{
            display: "flex",
            alignItems: "center",
            borderRadius: "8px",
            minWidth: "34px",
            minHeight: "34px",
            maxHeight: "34px",
            maxWidth: "34px",
            backgroundColor: darkTheme ? "#30363a" : "#FFF",
            boxShadow: darkTheme ? "0px 2px 8px -1px #050505b3" : "0px 2px 6px -1px #4e505166",
            marginRight: "16px",
          }}
        >
          {selectedItem?.favIconUrl ? (
            <motion.img
              //layout
              src={selectedItem.favIconUrl}
              height="24px"
              width="24px"
              style={{
                fontWeight: "500",
                margin: "auto",
                maxWidth: "24px",
                maxHeight: "24px",
              }}
            />
          ) : (
            <motion.div
              //layout
              style={{
                background: darkTheme ? "#373B3D" : "#E6EAEE",
                fontWeight: "500",
                width: "24px",
                height: "24px",
                maxWidth: "24px",
                maxHeight: "24px",
                borderRadius: "6px",
                margin: "auto",
              }}
            />
          )}
        </motion.div>
        <motion.div
          layout
          /* initial={{ opacity: 0, x: -12 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.38 }} */
          style={{ maxWidth: "84%" }}
        >
          {isLoadingSel || isLoading ? (
            <TitleSkeleton />
          ) : (
            <motion.div layout style={{ maxWidth: "100%" }}>
              <motion.p
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  marginTop: "auto",
                  marginBottom: "auto",
                  letterSpacing: "0.10px",
                  color: darkTheme ? "#FFF" : "#1a202c",
                  fontWeight: "500",
                  fontSize: "15px",
                  //fontFamily: fontStyle,
                }}
              >
                {selectedItem && selectedItem.title}
              </motion.p>
              <motion.p
                style={{
                  color: "#b0b7c1",
                  fontWeight: "500",
                  fontSize: "11px",
                  marginTop: "2px",
                  marginBottom: "0px",
                  //fontFamily: fontStyle,
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                }}
              >
                {selectedItem && selectedItem.url}
              </motion.p>
            </motion.div>
          )}
        </motion.div>
      </motion.div>
      <AnimatePresence>
        {isSaved !== "loading" && !isLoading && isSaved ? (
          <SavedInBar setCurrentSelectedColor={setCurrentSelectedColor} />
        ) : null}
      </AnimatePresence>
      <TextAreaComp
        initValue={newComment ? newComment.current : ""}
        isLoading={isLoading}
        newNote={true}
        commentColor={commentColor}
        setCurrentSelectedColor={setCurrentSelectedColor}
        index={-1}
        isTodo={isNewNoteTodo}
        setNewComment={setNewComment}
      />
      <ButtonsRow
        setIsLoading={setIsLoading}
        currentSelectedColor={currentSelectedColor}
        setCurrentSelectedColor={setCurrentSelectedColor}
        isLoading={isLoading}
      />
    </motion.div>
  );
};

export default CurrentPageBox;
