import React, { useState, useEffect, useRef, Children } from "react";
import useStore from "./store";

const TooltipComp = ({ top, left, right, label, ...props }) => {
  const darkTheme = useStore((state) => state.darkTheme);
  let bgColor = darkTheme ? "#D4D8DC" : "#343C49";
  return (
    <div
      style={{
        position: "absolute",
        left: left ? left : "",
        right: left ? "" : right,
        top: top,
        background: bgColor,
        borderRadius: "6px",
        zIndex: "99999999",
        whiteSpace: "nowrap",
      }}
    >
      {label && (
        <p style={{ color: darkTheme ? "#1a202c" : "#FFF", fontSize: "12px", padding: "6px 10px" }}>
          {label}
        </p>
      )}
      {props.children && props.children}
    </div>
  );
};

export default TooltipComp;
