/* global chrome */
import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { DashboardIcon } from "./customIcon";
import useStore from "./store";

const DashboardButton = ({ testD }) => {
  const [showTooltip, setShowTooltip] = useState(false);
  const darkTheme = useStore((state) => state.darkTheme);
  const setDarkTheme = useStore((state) => state.setDarkTheme);

  const mountAnimation = {
    scale: 1,
    y: 16,
    transition: { delay: 0.1 },
  };

  const buttonMountAnimation = {
    scale: 1,
    y: 0,
    //transition: { delay: 0.6 },
  };

  const hoverAnimation = {
    scale: 1.075,
    transition: { delay: 0, duration: 0.06 },
  };

  const goToDashboard = () => {
    //console.log("clicked");
    chrome.runtime.sendMessage({ msg: "openDashboard" });
  };

  let bg_dark = "rgb(73 79 82)";
  let boxShadow_dark =
    "0 2px 6px -2px rgba(3,4,5,0.50), inset 1px 1px 1px 0 rgba(154,160,164,0.40), inset -1px -1px 1px 0 #2A2F34";

  return (
    <>
      <motion.div
        style={{
          position: "absolute",
          right: "16px",
          bottom: "16px",
          display: "flex",
          height: "48px",
          width: "48px",
          minWidth: "48px",
          minHeight: "48px",
          boxShadow:
            darkTheme === true
              ? boxShadow_dark
              : "inset 2px 2px 2px 0 #FFFFFF, inset -2px -2px 2px 0 rgba(182,184,186,0.70), rgb(78 80 81 / 60%) 0px 6px 20px -2px",
          borderRadius: "14px",
          background: darkTheme === true ? bg_dark : "#F4F5F7",
          zIndex: "100",
          cursor: "pointer",
          pointerEvents: "auto",
        }}
        onMouseEnter={() => setShowTooltip(true)}
        onMouseLeave={() => setShowTooltip(false)}
        initial={{ scale: 0, y: 60 }}
        animate={buttonMountAnimation}
        whileHover={{ scale: 1.075 }}
        onClick={() => goToDashboard()}
        whileTap={{ scale: 0.96 }}
        //transition={{ delay: 0.0, duration: 0.06 }}
      >
        <DashboardIcon darkTheme={darkTheme} />
      </motion.div>
      {showTooltip && (
        <div
          style={{
            position: "absolute",
            right: "16px",
            bottom: "74px",
            //height: "32px",
            background: "#343C49",
            borderRadius: "6px",
            zIndex: "101",
          }}
        >
          <p style={{ color: "#FFF", fontSize: "14px", padding: "6px 10px" }}>Open dashboard</p>
        </div>
      )}
    </>
  );
};

export default DashboardButton;
