/* global chrome */
import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence, useDragControls } from "framer-motion";
import { useHotkeys } from "react-hotkeys-hook";
import {
  ColorPickerIcon,
  PinIcon,
  TodoIcon,
  CommentIcon,
  Spinner,
  SunIcon,
  MoonIcon,
} from "../customIcon";
import TooltipComp from "../TooltipComp";
import useOutsideClick from "../useOutsideClick";
import { commentBg } from "../globalStyles";
import useStore from "../store";

const ColorPickerComp = ({
  setShowColorPicker,
  setShowTooltip2,
  setCommentColor,
  commentColor,
  currentSelectedColor,
}) => {
  const darkTheme = useStore((state) => state.darkTheme);
  let bgColor = darkTheme ? "#3F454D" : "#FFF";

  const ref = useRef();
  /* useOutsideClick(ref, () => {
    console.log("Outsideclick!!!");
    setShowColorPicker(false);
  }); */
  const onColorChosen = (colorIndex) => {
    setShowTooltip2(false);
    setShowColorPicker(false);
    setCommentColor(colorIndex);
  };
  let timingProp = { duration: 0.1 };

  return (
    <div
      ref={ref}
      onClick={(e) => e.stopPropagation()}
      style={{
        position: "absolute",
        top: "26px",
        left: "-48px",
        padding: "12px",
        borderRadius: "10px",
        backgroundColor: bgColor,
        zIndex: "200000",
        boxShadow: "0px 4px 18px -4px #4e5051b3",
        display: "grid",
        gap: "10px",
        gridTemplateColumns: "repeat(4, 1fr)",
      }}
    >
      <motion.button
        whileHover={{ scale: 1.05 }}
        transition={timingProp}
        style={{
          padding: "0px",
          width: "24px",
          height: "22px",
          borderRadius: "5px",
          background: "#EDF2F7",
          alignItems: "center",
          boxShadow:
            currentSelectedColor === false
              ? "0px 0px 0px 2px " + bgColor + ", 0px 0px 0px 4px #A0AEC0"
              : "",
          display: "flex",
        }}
        onClick={() => onColorChosen(false)}
      ></motion.button>
      {commentBg.map((color, index) => (
        <motion.button
          whileHover={{ scale: 1.05 }}
          transition={timingProp}
          key={index}
          style={{
            padding: "0px",
            width: "24px",
            maxWidth: "24px",
            height: "22px",
            background: color.iconLight,
            borderRadius: "4px",
            boxShadow:
              currentSelectedColor === index
                ? "0px 0px 0px 2px " + bgColor + ", 0px 0px 0px 4px #A0AEC0"
                : "",
          }}
          onClick={() => onColorChosen(index)}
        ></motion.button>
      ))}
    </div>
  );
};

const ButtonsRow = ({ isLoading, setIsLoading, setCurrentSelectedColor, currentSelectedColor }) => {
  //zuStand
  const setShowLayout = useStore((state) => state.setShowLayout);
  const selectedItem = useStore((state) => state.selectedItem);
  const isSaved = useStore((state) => state.isSaved);
  const setCommentColor = useStore((state) => state.setCommentColor);
  const commentColor = useStore((state) => state.commentColor);
  const modifySelectedNote = useStore((state) => state.modifySelectedNote);
  const isNewNoteTodo = useStore((state) => state.isNewNoteTodo);
  const modifiedIndexes = useStore((state) => state.modifiedIndexes);
  const setModifiedIndex = useStore((state) => state.setModifiedIndex);
  const setShowToast = useStore((state) => state.setShowToast);
  const isLoadingUpdate = useStore((state) => state.isLoadingUpdate);
  const setIsLoadingUpdate = useStore((state) => state.setIsLoadingUpdate);
  const bringBackFocus = useStore((state) => state.bringBackFocus);
  const focusSavedNote = useStore((state) => state.focusSavedNote);
  const setFocusSavedNote = useStore((state) => state.setFocusSavedNote);
  const darkTheme = useStore((state) => state.darkTheme);
  const setDarkTheme = useStore((state) => state.setDarkTheme);

  let bgColor = darkTheme ? "#373B3D" : "#E6EAEE";

  const secondaryButtonStyles = {
    marginRight: "8px",
    cursor: "pointer",
    position: "relative",
    minHeight: "38px",
    minWidth: "50px",
  };
  const [showTooltip0, setShowTooltip0] = useState(false);
  const [showTooltip1, setShowTooltip1] = useState(false);
  const [showTooltip2, setShowTooltip2] = useState(false);
  const [showTooltip3, setShowTooltip3] = useState(false);
  const [isPinned, setIsPinned] = useState(false);

  useEffect(() => {
    if (selectedItem.pinned) {
      setIsPinned(true);
    }
  }, [selectedItem]);

  const pinTab = () => {
    let tabId = selectedItem.tabId;
    chrome.runtime.sendMessage({ msg: isPinned ? "unPinTab" : "pinTab", tabId: tabId }, (res) => {
      let pinned = isPinned ? false : true;
      setIsPinned(pinned);
      let mess = "Tab " + (pinned ? "pinned" : "unpinned");
      setShowToast("success", mess, false);
    });
  };
  const [showColorPicker, setShowColorPicker] = useState(false);
  const [isTodo, setIsTodo] = useState(false);
  useEffect(() => {
    if (focusSavedNote === -1) {
      setIsTodo(isNewNoteTodo);
    } else {
      let todoStatus = isSaved[focusSavedNote].itemObj.todo;
      setIsTodo(todoStatus);
    }
  }, [focusSavedNote]);

  const toggleTodo = () => {
    setIsTodo(!isTodo);
    if (focusSavedNote === -1) {
      modifySelectedNote(!isTodo, "todo", -1);
      bringBackFocus(true);
    } else {
      modifySelectedNote(!isTodo, "todo", focusSavedNote);
      bringBackFocus(true);
    }
  };

  const setCommentColorMiddleware = (colorIndex) => {
    if (focusSavedNote === -1) {
      setCurrentSelectedColor(colorIndex);
      setCommentColor(colorIndex);
      bringBackFocus(true);
    } else {
      modifySelectedNote(colorIndex, "commentColor", focusSavedNote);
      setCurrentSelectedColor(colorIndex);
      bringBackFocus(true);
    }
  };

  const saveOrUpdateMiddleware = () => {
    if (focusSavedNote === -1) {
      setShowLayout("saveView");
    } else {
      updateSavedItem();
    }
  };

  const updateSavedItem = () => {
    const itemToUpdate = isSaved[focusSavedNote];

    let msg = "updateItemInWS";
    if (itemToUpdate.sharedRefId) {
      // TODO dubbelkolla att  denna uppdaterar shared cat
      msg = "updateItemInWSShared";
    }
    setIsLoadingUpdate(true);

    chrome.runtime.sendMessage(
      {
        msg: msg,
        groupIndex: itemToUpdate.groupIndex,
        itemIndex: itemToUpdate.itemIndex,
        refId: itemToUpdate.refId,
        itemObj: itemToUpdate.itemObj,
        isStacked: itemToUpdate.isStacked,
        stackIndex: itemToUpdate.stackIndex,
        type: "tab",
        ...(itemToUpdate.sharedRefId && { sharedRefId: itemToUpdate.sharedRefId }),
      },
      (res) => {
        //console.log("res: ", res);
        if (res.msg === "error") {
          setShowToast("error", "Error updating note", false);
        } else {
          setIsLoadingUpdate(false);
          let newArr = modifiedIndexes.filter((i) => i !== focusSavedNote);
          setModifiedIndex(newArr);
          setCurrentSelectedColor(false);
          setFocusSavedNote(-1);
          setShowToast("success", "Note updated", false);
          //showToasterMessage("success", 2800);
        }
      }
    );
  };

  let enableUpdateButton =
    modifiedIndexes.includes(focusSavedNote) || focusSavedNote === -1 ? true : false;

  let buttonTiming1 = { /* scale: { delay: 0.3 },  */ backgroundColor: { duration: 0.1 } };
  let buttonTiming2 = { /* scale: { delay: 0.34 }, */ backgroundColor: { duration: 0.1 } };
  let buttonTiming3 = { /* scale: { delay: 0.38 }, */ backgroundColor: { duration: 0.1 } };
  let buttonTiming4 = { /* scale: { delay: 0.42 }, */ backgroundColor: { duration: 0.1 } };
  let buttonTiming5 = {
    /* scale: { delay: 0.46 }, */ backgroundImage: { duration: 0.1 },
    duration: 0.5,
  };

  //let buttonBg = currentSelectedColor
  let hoverColor = darkTheme ? "#494C4F" : "#cad1d8";
  let tapColor = darkTheme ? "#55595D" : "#ABB5BF";

  return (
    <>
      <motion.div
        layout="position"
        style={{ display: "flex", margin: "8px 16px", minHeight: "38px" }}
      >
        <motion.button
          animate={{ backgroundColor: bgColor }}
          transition={buttonTiming1}
          whileHover={{ backgroundColor: hoverColor }}
          whileTap={{ backgroundColor: tapColor }}
          style={{
            marginLeft: "auto",
            backgroundColor: bgColor,
            ...secondaryButtonStyles,
          }}
          onMouseEnter={() => setShowTooltip0(true)}
          onMouseLeave={() => setShowTooltip0(false)}
          onClick={() => setDarkTheme(!darkTheme)}
        >
          {darkTheme ? <SunIcon /> : <MoonIcon />}
          {showTooltip0 && <TooltipComp label={"Toggle darkmode"} top={"44px"} right={"-32px"} />}
        </motion.button>
        <motion.button
          animate={{ backgroundColor: bgColor }}
          transition={buttonTiming2}
          whileHover={{ backgroundColor: hoverColor }}
          whileTap={{ backgroundColor: tapColor }}
          style={{
            backgroundColor: bgColor,
            ...secondaryButtonStyles,
          }}
          onMouseEnter={() => setShowTooltip1(true)}
          onMouseLeave={() => setShowTooltip1(false)}
          onClick={toggleTodo}
        >
          {isTodo ? <CommentIcon /> : <TodoIcon />}
          {showTooltip1 && (
            <TooltipComp
              label={isTodo ? "Turn into note" : "Turn into To-do"}
              top={"44px"}
              right={"-28px"}
            />
          )}
        </motion.button>
        <div style={{ position: "relative" }}>
          <motion.button
            animate={{
              backgroundColor: showTooltip2
                ? currentSelectedColor !== false
                  ? commentBg[currentSelectedColor].iconLight
                  : hoverColor
                : currentSelectedColor !== false
                ? commentBg[currentSelectedColor].bg
                : bgColor,
            }}
            whileTap={{ backgroundColor: tapColor }}
            transition={buttonTiming3}
            style={{ ...secondaryButtonStyles }}
            onMouseEnter={() => setShowTooltip2(true)}
            onMouseLeave={() => {
              setShowTooltip2(false);
              setShowColorPicker(false);
            }}
            onClick={(e) => {
              e.stopPropagation();
              e.preventDefault();
              setShowColorPicker(!showColorPicker);
            }}
          >
            <ColorPickerIcon
              color={
                currentSelectedColor !== false
                  ? commentBg[currentSelectedColor].text
                  : darkTheme
                  ? "#FFF"
                  : "#4A5568"
              }
            />
            {showTooltip2 && !showColorPicker && (
              <TooltipComp
                label={showColorPicker ? "Close color-picker" : "Change color"}
                top={"44px"}
                right={"-24px"}
              />
            )}
            {showColorPicker && showTooltip2 && (
              <ColorPickerComp
                setShowColorPicker={setShowColorPicker}
                setShowTooltip2={setShowTooltip2}
                setCommentColor={setCommentColorMiddleware}
                commentColor={commentColor}
                currentSelectedColor={currentSelectedColor}
              />
            )}
          </motion.button>
        </div>
        <motion.button
          animate={{ backgroundColor: bgColor }}
          transition={buttonTiming4}
          whileHover={{ backgroundColor: hoverColor }}
          whileTap={{ backgroundColor: tapColor }}
          style={{ backgroundColor: bgColor, ...secondaryButtonStyles }}
          onMouseEnter={() => setShowTooltip3(true)}
          onMouseLeave={() => setShowTooltip3(false)}
          onClick={() => pinTab()}
        >
          <PinIcon isPinned={isPinned} />
          {showTooltip3 && (
            <TooltipComp label={isPinned ? "Unpin tab" : "Pin tab"} top={"44px"} right={"-6px"} />
          )}
        </motion.button>
        <motion.button
          initial={{ scale: 1 }}
          animate={{ scale: 1 }}
          whileTap={{ backgroundColor: "#ABB5BF" }}
          transition={buttonTiming5}
          onAnimationComplete={() => {
            setIsLoading(false);
          }}
          whileHover={{ backgroundImage: "linear-gradient(145deg, #3592FE 0%, #2E7EDB 100%)" }}
          style={{
            backgroundImage: "linear-gradient(145deg, #5CABFF 0%, #3693FF 100%",
            boxShadow: "0 6px 8px -3px rgba(49,144,255,0.54",
            letterSpacing: "0.32px",
            cursor: "pointer",
            minWidth: "78px",
            maxHeight: "38px",
            minHeight: "38px",
            paddingTop: "9px",
            opacity: enableUpdateButton && !isLoadingUpdate ? "1" : "0.5",
            cursor: enableUpdateButton && !isLoadingUpdate ? "pointer" : "not-allowed",
          }}
          disabled={!enableUpdateButton}
          onClick={() => saveOrUpdateMiddleware()}
        >
          {isSaved !== "loading" && !isLoading && !isLoadingUpdate ? (
            focusSavedNote !== -1 ? (
              "Update"
            ) : (
              "Save"
            )
          ) : (
            <Spinner fillColor={"#FFF"} trackColor={"#7dbbff"} maxW={"20px"} />
          )}
        </motion.button>
      </motion.div>
    </>
  );
};

export default ButtonsRow;
