import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { CloseIcon, CopySel, DragHandleIcon, TrashIcon } from "../customIcon";
import TooltipComp from "../TooltipComp";
import useStore from "../store";

const MiniToolBar = ({ setAreyousure, areyousure, index, note, isStacked }) => {
  const [showTooltip1, setShowTooltip1] = useState(false);
  const [showTooltip2, setShowTooltip2] = useState(false);
  const [isCopied, setTextCopied] = useState(false);
  const darkTheme = useStore((state) => state.darkTheme);
  const copyToClipboard = () => {
    if (note) {
      navigator.clipboard.writeText(note);
      setTextCopied(true);
    }
  };
  const timingProp = { duration: 0.1 };

  let hoverColor = darkTheme ? "#292C2F" : "#EEF0F4";
  let tapColor = darkTheme ? "#232527" : "#CBD5E0";
  let tooltipBgColor = darkTheme ? "#D4D8DC" : "#343C49";

  return (
    <div
      style={{ marginLeft: "auto", display: "flex", position: "relative", alignItems: "center" }}
    >
      <motion.button
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        whileHover={{ background: hoverColor }}
        whileTap={{ background: tapColor }}
        style={{
          //background: "#eef0f400",
          background: "transparent",
          height: "32px",
          width: "32px",
          alignItems: "center",
          padding: "0px",
        }}
        onMouseEnter={() => setShowTooltip1(true)}
        onMouseLeave={() => setShowTooltip1(false)}
        onClick={() => copyToClipboard()}
      >
        <CopySel />
      </motion.button>
      {showTooltip1 && (
        <motion.div
          //layout
          transition={timingProp}
          style={{
            position: "absolute",
            right: "6px",
            top: "38px",
            background: tooltipBgColor,
            borderRadius: "6px",
            zIndex: "99999999",
            whiteSpace: "nowrap",
            display: "flex",
          }}
        >
          <motion.p
            transition={timingProp}
            style={{
              color: isCopied
                ? darkTheme
                  ? "#1a202c"
                  : "#9AE6B4"
                : darkTheme
                ? "#1a202c"
                : "#FFF",
              fontSize: "12px",
              padding: "6px 10px",
              margin: "auto",
              opacity: note ? "1" : "0.75",
            }}
          >
            {!note ? "Nothing to copy" : isCopied ? "✓ Copied to clipboard!" : "Copy content"}
          </motion.p>
        </motion.div>
      )}
      <motion.button
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        whileHover={{ background: hoverColor }}
        whileTap={{ background: tapColor }}
        style={{
          background: "transparent",
          marginLeft: "4px",
          marginRight: "0px",
          height: "32px",
          width: "32px",
          alignItems: "center",
          padding: "0px",
        }}
        onMouseEnter={() => setShowTooltip2(true)}
        onMouseLeave={() => setShowTooltip2(false)}
        onClick={() => setAreyousure(index)}
      >
        <TrashIcon />
        {showTooltip2 && areyousure !== index && (
          <TooltipComp
            label={isStacked ? "Delete item from stack" : "Delete item"}
            top={"38px"}
            right={"-20px"}
          />
        )}
      </motion.button>
    </div>
  );
};

export default MiniToolBar;
